#data {
    display: flex;
    padding: 1rem;
    margin-bottom: 500px;
    margin-left: 27%;


    #left {
        justify-self: center;
        margin: 0;
        padding: 1rem;
        width: 450px;

        h1 {
            color: white;
            margin: 0;
            margin-bottom: 3vh;
            font-family: "Söhne Breit Test";
            font-weight: 100;
            font-size: 120px;
            line-height: 117px;
            text-align: justify;
            display: inline-block;
        }

        h1::after {
            z-index: -1;
            position: absolute;
            display: inline-block;
            content: "";
            top: -55px;
            left: 225px;
            // background: linear-gradient(to top, rgb(93, 93, 223), rgb(39, 39, 250));
            background-image: url('./../img/button.png');
            background-size: cover;
            width: 180px;
            height: 180px;
            transform: rotate(-135deg);
        }

        h1:hover::after {
            border: 2px solid #FFFFFF;
            background: transparent;
            transition: all 0.5s ease-out;
            -webkit-box-shadow: 0px 0px 0px 5px rgb(39, 39, 250); 
            box-shadow: 0px 0px 0px 5px rgb(39, 39, 250);
        }

        p {
            margin: 0;
            display: inline-block;
            font-family: "Founders Grotesk Condensed Test Medium";
            font-style: normal;
            font-weight: 500;
            font-size: 27px;
            white-space: nowrap;
            position: relative;
            line-height: 27px;
            color: #E5E5E5;
        }

    }
    #right {
        margin-top: 2vh;
        justify-self: center;
        display: grid;
        width: 300px;

        .anim {
            opacity: 0;
        }

        .Starters {
            grid-column: 1/2;
            grid-row: 1/5;
        }

        .Starters1 {
            margin-left: 7px;
            margin-top: 11px;
            grid-row: 1/5;
            grid-column: 2/2;
            
            img::before {
                width: 0%;    
            }

            img {
            width: 310%;
            height: 24px;
            }
        }
        .Good {
            grid-row: 5;
            grid-column: 1/2;
        }

        .Good1 {
            margin-left: 7px;
            margin-top: 11px;
            grid-row: 5;
            grid-column: 2/2;
            
            img::before {
                width: 0%;    
            }
            
            img {
            width: 197%;
            height: 24px;
            }
        }
        .Great {
            grid-column: 1/2;
            grid-row: 6;
        }

        .Great1 {
            margin-left: 7px;
            margin-top: 11px;
            grid-row: 6;
            grid-column: 2/2;
            
            img::before {
                width: 0%;    
            }

            img {
            width: 100%;
            height: 24px;
            }
        }

        .SuperStar {
            grid-column: 1/2;
            grid-row: 7;
        }

        .SuperStar1 {
            margin-left: 7px;
            margin-top: 11px;
            grid-row: 7;
            grid-column: 2/2;

            img::before {
                width: 0%;    
            }

            img {
            width: 47%;
            height: 24px;
            }
        }

        
        .Icon {
            grid-column: 1/2;
            grid-row: 8;
        }
        
        .Icon1 {
            margin-left: 7px;
            margin-top: 11px;
            grid-row: 8;
            grid-column: 2/2;
            
            img::before {
                width: 0%;    
            }
            
            img {
            width: 12%;
            height: 24px;
        }
    }
    
    .test {
        opacity: 0;
    }

    p {
        color: white;
        font-weight: 400;
        margin: 6px;
        font-family: "Söhne Breit Test";
        font-size: 18px;
        line-height: 30px;
        text-align: right;
        white-space: nowrap;
        animation: fadein 2s;
        -moz-animation: fadein 2s;
        -webkit-animation: fadein 2s;
        -o-animation: fadein 2s;
    }

    @keyframes slidein {
        from { width: 0%;}
      }
    

        @keyframes fadein {
            from {
                opacity:0;
            }
            to {
                opacity:1;
            }
        }
        @-moz-keyframes fadein { /* Firefox */
            from {
                opacity:0;
            }
            to {
                opacity:1;
            }
        }
        @-webkit-keyframes fadein { /* Safari and Chrome */
            from {
                opacity:0;
            }
            to {
                opacity:1;
            }
        }
        @-o-keyframes fadein {
            from {
                opacity:0;
            }
            to {
                opacity: 1;
            }
        }
    }
}


