#drop {

    margin: 0;
    display: flex;
    margin-bottom: 100vh;

    .right {
        z-index: 1;
        display: inline-block;
        right: 100px;
    }

    .left {
        z-index: 2;
        width: 550px;
        position: absolute;
        right: 300px;
        display: inline-block;
        margin-bottom: 20vh;
    }

    .left::after {
        position: relative;
        display: inline-block;
        content: "";
        z-index: -1;
        top: -315px;
        right: -530px;
        // background: linear-gradient(to top, rgb(39, 39, 250), rgb(93, 93, 223));
        background-image: url('./../img/background.png');
        background-size: contain contain;
        width: 48px;
        height: 340px;
    }

    .left:hover::after {
        border: 2px solid #FFFFFF;
        background: transparent;
        transition: all 0.5s ease-out;
        // animation: animate 1s linear forwards;
        -webkit-box-shadow: 0px 0px 0px 5px rgb(39, 39, 250); 
        box-shadow: 0px 0px 0px 5px rgb(39, 39, 250);
      }

    @keyframes animate {
        0% {
            filter: blur(0);
        }
  
        40% {
            filter: blur(20px);
        }
  
        80% {
            filter: blur(0);
        }
  
        100% {
            filter: blur(0);
        }
    }

    .space_top {
        margin-top: 33px;
    }

    h1 {
        color: white;
        font-family: "Söhne Breit Test";
        font-size: 35px;
        line-height: 33px;
        text-align: right;
        margin: 0;
        white-space: nowrap;
      }

    h2 {
        color: white;
        font-family: "Founders Grotesk Condensed Test Semibold";
        font-style: normal;
        font-weight: 500;
        font-size: 30px;
        line-height: 25px;
        text-align: right;
        margin: 0;
        white-space: nowrap;
    }

    p {
        color: white;
        font-family: "Founders Grotesk Condensed Test Medium";
        font-style: normal;
        font-size: 20px;
        margin: 0;
        line-height: 20px;
        text-align: right;
    }
}
@media only screen and (max-width: 1300px) {
    #drop {

        .left {
            right: 200px;
        }
    }
}

@media only screen and (max-width: 1000px) {
    #drop {
        margin-bottom: 320px;
        padding-bottom: 290px;
        
        .left {
            right: 130px;
            width: 500px;
        }

        .left::after {
            position: relative;
            display: inline-block;
            content: "";
            z-index: -1;
            top: -315px;
            right: -480px;
            background-image: url('./../img/background.png');
            background-size: contain contain;
            width: 48px;
            height: 330px;
        }
        h1 {
            font-size: 35px;
            line-height: 33px;
          }
    
        h2 {
            font-size: 25px;
            line-height: 30px;
            text-align: right;
        }
    
        p {
            font-size: 18px;
            line-height: 22px;
            text-align: right;
        }
    }

  }
  
  @media only screen and (max-width: 700px) {
