        .left {
            left:0px;
            width: 90vw;
            // width: 315px;
            right: 0;
        }

        .left::after {
            top: -290px;
            right: -84vw;
            // right: -305px;
            width: 40px;
            height: 300px;
        }

        .space_top {
            margin-top: 24px;
        }
        h1 {
            font-size: 24px;
            line-height: 26px;
          }
    
        h2 {
            font-size: 20px;
            line-height: 24px;
            text-align: right;
        }
    
        p {
            font-size: 16px;
            line-height: 20px;
            text-align: right;
        }
    }
  }