#Pack {
    position: relative;
    margin: 0 auto;
    // left: 350px;
    padding: 0px 0px 240px 0px;
    max-width: 900px;
    
    .center {
        height: 450px;
        display: flex;
        
        .left {
            border: 1px solid rgba(255, 255, 255, 0.2);
            width: 50%;
            // background: linear-gradient(85.96deg, rgba(255, 255, 255, 0) -20.51%, rgba(255, 255, 255, 0.05) 26.82%, rgba(255, 255, 255, 0) 65.65%), rgba(196, 196, 196, 0.05);
            // box-shadow: -6px 6px 28px rgba(0, 0, 0, 0.47), inset 0px -2.50245px 1.25122px rgba(255, 255, 255, 0.1);
            backdrop-filter: blur(37.5367px);
            /* Note: backdrop-filter has minimal browser support */

            .align {
                padding: 60px 60px 60px 60px;

            h2 {
                font-family: "Söhne Breit Test";
                font-size: 30px;
                line-height: 35px;
                margin: 0; 
                color: #FFFFFF; 
            }

            h3 {
                font-family: "Founders Grotesk Condensed Test Medium";
                font-style: normal;
                font-weight: 500;
                font-size: 24px;
                line-height: 29px;
                text-align: justify;
                color: #D9D9D9;
            }
        }
        }
        .left:hover {
            backdrop-filter: blur(0);
        }

        .right {
            width: 50%;
            // border-radius: 10px;
            background-image: url('./../img/rectangleCarrousel.png');
            // background-size: contain contain;
            background-position: center center;
            background-repeat: no-repeat;
            background-size: cover;
        }
    }
}

@media only screen and (max-width: 1100px) {
#Pack {
    position: relative;
    margin: 0 auto;
    // left: 350px;
    padding: 40px 0px 240px 0px;
    max-width: 800px;
        .center {
            height: 450px;
            display: flex;
        }
    }
}

