header {
    font-size: 50px;
    line-height: 29px;
    font-family: "Söhne Breit Test";
    margin-bottom: 500px;
    text-align: center;

    .center {
      margin-top:40vh;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 50px;
      line-height: 50px;
      font-family: "Söhne Breit Test";
      animation: opac 2s linear forwards;
      
      .meta {
        display: flex;  
          h1 {
              color: transparent;
              -webkit-text-stroke-width: 2px;
              -webkit-text-stroke-color: #fff;
              margin: 2px;
              text-transform: uppercase;
              position: relative;
              animation: animate 2s linear forwards;
            }

            h1:nth-child(1) {
                animation-delay: 0.1s;
            }
        
            h1:nth-child(2) {
                animation-delay: 0.2s;
            }
        
            h1:nth-child(3) {
                animation-delay: 0.4s;
            }
        
            h1:nth-child(4) {
                animation-delay: 0.5s;
            }
        
    }
    
    .Manager {
        margin-left: 40px;
        color: white;
        display: flex;
        animation: opac 2s linear forwards;
        
        h1 {
            color: white;
            animation: animate 2s linear forwards;
            margin: 2px;
        }

        h1:nth-child(1) {
            animation-delay: 0.7s;
        }
    
        h1:nth-child(2) {
            animation-delay: 0.8s;
        }
        h1:nth-child(3) {
            animation-delay: 0.9s;
        }
    
        h1:nth-child(4) {
            animation-delay: 1s;
        }
    
        h1:nth-child(5) {
            animation-delay: 1.1s;
        }
    
        h1:nth-child(6) {
            animation-delay: 1.2s;
        }
    
        h1:nth-child(7) {
            animation-delay: 1.3s;
        }
    
        h1:nth-child(8) {
            animation-delay: 1.4s;
        }
      }

    @keyframes animate {
        0% {
            filter: blur(0);
        }

        40% {
            filter: blur(20px);
        }

        80% {
            filter: blur(0);
        }

        100% {
            filter: blur(0);
        }
    }

    @keyframes opac {
        from {
            opacity: 0;
        }
        to {
            opacity: 1;
        }
    }

    }
  }
  
  @media only screen and (max-width: 1200px) {

    header {
        // margin-bottom: 350px;

      .center {
        // margin-top:350px;
        font-size: 43px;
        line-height: 43px;

      }
    }
}

  @media only screen and (max-width: 1000px) {

    header {
        margin-bottom: 350px;

      .center {
        // margin-top:400px;
        font-size: 37px;
        line-height: 30px;

      }
    }
}

@media only screen and (max-width: 850px) {

    header {
      .center {
        margin-top:320px;
        font-size: 35px;
        line-height: 40px;
        flex-direction: column;

        .Manager {
            margin-left: 0;
            margin: 20px;
        }
      }
    }
}

@media only screen and (max-width: 600px) {

    header {
      .center {
        margin-top:270px;
        font-size: 25px;
        line-height: 25px;
        flex-direction: column;

        .Manager {
            margin-left: 0;
            margin: 20px;
        }
      }
    }
}