#about {
        padding: 1rem;
        width: 80%;
        padding-top: 200px;
        margin-bottom: 700px;

        div {
            position: absolute;
            left: 11vw;
            width: 40%;
            margin-top: 2vh;
          }

        hr {
            width: 100%; 
            overflow: hidden;
            transition:width 1s;
            height:2px;
            background: #fff;
            position: absolute;
            left: 200px;
            margin: 0vh;
          }
          
         @keyframes slidein {
          from { left: 0; }
          to   { left: 50%; }
        }

          .rotate1::before {
            position: absolute;
            display: inline-block;
            content: "ABOUT";
            top: 80px;
            left: -200px;
            font-family: "Söhne Breit Test";
            font-size: 70px;
            line-height: 47px;
            text-align: justify;
            text-transform: capitalize;
            font-weight: 500;
            color: #FFFFFF;
            text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.46);
            transform: rotate(-90deg);
          }

          .rotate1 {
            margin-bottom: 10px;
          }

        p {
          position: absolute;
          left: 200px;
          font-family: "Founders Grotesk Condensed Test Medium";
          opacity: 0;
          font-style: normal;
          width: 100%;
          font-weight: 100;
          font-size: 18px;
          line-height: 22px;
          text-align: justify;
          position: relative;
          color: #E5E5E5;
          display: inline-block;
        }

}


