@import url("https://css.gg/chevron-left.css");
@import url("https://css.gg/chevron-right.css");

#Calendar1 {
  background-color: black;
  width: 100%;
  padding: 0;
  overflow-x: hidden;
  padding-bottom: 5vh;
  // border-top: 1px solid rgba(255, 255, 255, 0.2);
  .title_calendar_mobile {
    display: none;
  }

  .title_calendar {
    text-align: center;
    margin-bottom: 0vh;
    margin-top: 1vh;

    h1 {
      color: white;
      font-family: "Söhne Breit Test";
      font-size: 50px;
      line-height: 50px;
      text-align: center;
      display: inline-block;
    }

    h1::before {
      position: relative;
      display: inline-block;
      content: "";
      top: -13px;
      left: -28px;
      background-image: url("./../img/button.png");
      background-size: cover;
      width: 12px;
      height: 12px;
      transform: rotate(-135deg);
    }

    h1::after {
      position: relative;
      display: inline-block;
      content: "";
      top: -13px;
      right: -28px;
      background-image: url("./../img/button.png");
      background-size: cover;
      width: 12px;
      height: 12px;
      transform: rotate(-135deg);
    }

    h3 {
      font-family: "Founders Grotesk Condensed Test Medium";
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 29px;
      text-align: center;
      color: #d9d9d9;
      margin: 0;
    }
    .sous_title {
      text-align: center;
    }
  }

  .title {
    font-size: 25px;
    color: #fff;
    font-weight: bold;
    margin-bottom: 25px;
  }
  .container {
    margin-top: 100px;
    border-radius: 40px;
    background: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .item__mobile {
    margin: 40px 80px;
    transition: transform 1s;
    transform: translate3d(0, -1px, 0);
    position: relative;
  }

  .item__mobile img {
    display: block;
    width: 0%;
    max-width: 530px;
    margin: auto;
    height: auto;
    opacity: 0.5;
    transition: transform 1s;
  }
  .active_image > img {
    opacity: 1;
    transition: transform 1s;
  }
  .item__body {
    padding: 25px;
  }

  .item__title {
    font-size: 24px;
    margin-bottom: 5px;
    font-weight: bold;
    color: #232323;
  }

  .item__description {
    color: #585858;
  }

  .items {
    position: relative;
  }

  /**
        CAROUSEL
         */
  .carousel {
    position: relative;
    width: 140%;
    transform: translateX(-14.3%);
  }

  .carousel__container {
    transition: transform 1s;
    display: flex;
    align-items: center;
    justify-content: space-around;

    transform: translate3d(0, 0, 0);
  }

  .carousel__container::after {
    content: "";
    clear: both;
    display: table;
  }

  .carousel__item {
    float: left;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .progress__container {
    width: 110%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    margin-top: 40px;
  }
  .void {
    width: 20%;
  }
  .button__container {
    padding-right: 220px;
    background-color: #fefefe;
    height: 2px;
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    gap: 10px;
    position: relative;
    align-items: center;
    box-shadow: 0 0 1px #fff, 0 0 1px #fff, 0 0 5px #fff;
    position: absolute;
  }
  #carousel__prev {
    border: solid 1px #fff;
    border-radius: 50%;
    background-color: #000000;
    width: 40px;
    height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    // cursor: pointer;
    transition: 0.5s ease;
  }

  #carousel__next {
    border: solid 1px #fff;
    border-radius: 50%;
    background-color: #000000;
    width: 40px;
    height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    // cursor: none;
    transition: 0.5s ease;
  }
  #carousel__prev:active,
  #carousel__next:active {
    transform: scale(1.1);
    transition: 0.5s ease;
  }
  .gg-chevron-left,
  .gg-chevron-right {
    color: white;
  }
  .cursor {
    width: 20px;
    height: 20px;
    transform: rotate(45deg);
    background: linear-gradient(to right, rgb(39, 39, 255), rgb(93, 93, 223));
    justify-self: flex-start !important;
    position: absolute;
    left: 0;
  }
  .item__content {
    max-width: 80%;
    transform: translate(-150px, -180px);
    position: absolute;
    padding: 10px;
    display: none;
  }
  .item__content > * {
    color: #fff;
  }
  .item__content > p {
    text-align: left;
    font-family: "Founders Grotesk" Condensed Test;
    font-size: 15px;
    font-style: normal;
    font-weight: 500;
    line-height: 15px;
    letter-spacing: 0em;
    text-align: left;
  }
  .item__content > h5 {
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
  }
  .item__content > h3 {
    font-size: 45px;
    line-height: 45px;
    letter-spacing: 0em;
    text-align: left;
    background-image: linear-gradient(
        to bottom,
        rgba(80, 143, 251, 0.2) 0%,
        rgba(82, 21, 250, 0.2) 99.99%,
        rgba(0, 0, 0, 0) 100%
      ),
      linear-gradient(0deg, #4760fb, #4760fb),
      linear-gradient(0deg, #2dffa6, #2dffa6);
    background-size: 100%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
  }
  .active_item {
    display: block;
  }
}

@media screen and (max-width: 800px) {

  .carousel__pagination {
    display: flex;
  }
  .wrapper {
    display: none;
  }
  .item__content {
    max-width: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translate(0%, 0%);
    position: absolute;
    padding: 10px;
    display: none;
  }
