/**
    CAROUSEL
     */
.carousel__mobile {
  position: relative;
  width: 90%;
}

.carousel__container__mobile {
  transition: transform 1s;
  display: flex;
  align-items: center;
  justify-content: space-around;

  transform: translate3d(0, 0, 0);
}

.carousel__container__mobile::after {
  content: "";
  clear: both;
  display: table;
}

.carousel__item {
  float: left;
  display: flex;
  align-items: center;
  justify-content: center;
}
#carousel__prev__mobile {
  border: solid 1px #fff;
  border-radius: 50%;
  background-color: #000000;
  width: 40px;
  height: 40px;
  display: flex;
  position: absolute;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  top: 50%;
  left: 0;
  transition: 0.5s ease;
}

#carousel__next__mobile {
  border: solid 1px #fff;
  border-radius: 50%;
  background-color: #000000;
  width: 40px;
  height: 40px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  right: 0;
  transition: 0.5s ease;
}
#carousel__next__mobile:active,
#carousel__prev__mobile:active {
  transform: scale(1.1);
  transition: 0.5s ease;
}
.gg-chevron-left,
.gg-chevron-right {
  color: white;
}
.item__content__mobile {
  max-width: 80%;
  position: absolute;
  transform: translate(0%, -10%);
  padding: 26px;
  display: none;
}
.item__content__mobile > * {
  color: #fff;
}
.item__content__mobile > p {
  text-align: left;
  font-family: "Founders Grotesk" Condensed Test;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;
}
.item__content__mobile > h5 {
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: 25px;
  letter-spacing: 0em;
  text-align: left;
}
.wrapper__mobile {
  position: relative;
}
.item__content__mobile > h3 {
  font-size: 40px;
  line-height: 30px;
  letter-spacing: 0em;
  text-align: left;
  background-image: linear-gradient(
      to bottom,
      rgba(80, 143, 251, 0.2) 0%,
      rgba(82, 21, 250, 0.2) 99.99%,
      rgba(0, 0, 0, 0) 100%
    ),
    linear-gradient(0deg, #4760fb, #4760fb),
    linear-gradient(0deg, #2dffa6, #2dffa6);
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}
.active_item {
  display: block;
}
.wrapper__mobile {
  display: none;
}
.active_image_mobile {
  opacity: 1 !important;
}
.carousel__next--hidden,
.carousel__prev--hidden {
  opacity: 0;
}

.carousel__pagination {
  text-align: center;
  display: none;
  position: absolute;
  bottom: 34%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.carousel__pagination__button {
  background-color: transparent;
  width: 10px;
  height: 10px;
  border: solid 1px #fff;
  border-radius: 50%;
  display: inline-block;
  margin: 0 8px;
  box-shadow: 0 2px 5px #3232321a;
  transition: transform 0.3s;
  cursor: pointer;
}

.carousel__pagination__button--active,
.carousel__pagination__button:hover {
  background-color: #fff;
}
