#roadmap {
    background-color: #080101;
    padding-top: 20vh;
    padding-bottom: 100px;
    width: 100%;

    hr {width: 123%; margin-left: -9vw; height:1px; background: #fff }

    .flexx {
        display: flex;
        width: 100%;
        opacity: 1;
    }

    .title {
        text-align: center;
        margin-bottom: 20vh;

        h3 {
            font-family: "Founders Grotesk Condensed Test Medium";
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 29px;
            text-align: center;
            color: #D9D9D9;
            margin: 0;
        }
    }

    .left {
        margin-top: 2vh;
        width: 30%;
        text-align: right;
    }

    .right {
        margin-bottom: 5vh;
        margin-left: 3vw;
        width: 42%;
    }

    .left2 {
        margin-top: 4vh;
        width: 30%;
        text-align: right;
    }

    .right2 {
        margin-bottom: 5vh;
        margin-left: 3vw;
        width: 42%;

        h2 {
            font-family: "Söhne Breit Test";
            font-size: 30px;
            line-height: 35px;
            margin: 0;
        }
    }

    h1 {
        font-family: "Söhne Breit Test";
        font-size: 50px;
        line-height: 50px;
        text-align: center;
        display: inline-block;
      }

    h1::before {
        position: relative;
        display: inline-block;
        content: "";
        top: -13px;
        left: -28px;
        // background-image: url('./../img/button.png');
        background: linear-gradient(to top, rgb(93, 93, 223), rgb(39, 39, 250));
        background-size: cover;
        width: 12px;
        height: 12px;
        transform: rotate(-135deg);
    }

    h1:hover::before {
        border: 2px solid #FFFFFF;
        background: transparent;
        transition: all 0.5s ease-out;
        // animation: animate 1s linear forwards;
        -webkit-box-shadow: 0px 0px 0px 2px rgb(39, 39, 250); 
        box-shadow: 0px 0px 0px 2px rgb(39, 39, 250);
    }

    h1::after {
        position: relative;
        display: inline-block;
        content: "";
        top: -13px;
        right: -28px;
        // background-image: url('./../img/button.png');
        background: linear-gradient(to top, rgb(93, 93, 223), rgb(39, 39, 250));
        background-size: cover;
        width: 12px;
        height: 12px;
        transform: rotate(-135deg);
    }
    h1:hover::after {
        border: 2px solid #FFFFFF;
        background: transparent;
        transition: all 0.5s ease-out;
        // animation: animate 1s linear forwards;
        -webkit-box-shadow: 0px 0px 0px 2px rgb(39, 39, 250); 
        box-shadow: 0px 0px 0px 2px rgb(39, 39, 250);
    }

    h2 {
        font-family: "Söhne Breit Test";
        font-size: 30px;
        line-height: 35px;
        margin: 0; 
        color: #FFFFFF;   
    }
    h3 {
        font-family: "Founders Grotesk Condensed Test Medium";
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 23px;
        text-align: justify;
        color: #D9D9D9;
        margin-bottom: 7vh;
        margin-top: 3vh;
    }

    .sous_title {
        text-align: center;
    }
}
    @media only screen and (max-width: 1400px) {
        #roadmap {
            hr {width: 123%; margin-left: -11vw;}
            .title {
                text-align: center;
                margin-bottom: 20vh;
            }
            
            .right {
                margin-bottom: 5vh;
                margin-left: 3vw;
                width: 42%;
            }
            .left {
                margin-top: 2vh;
                width: 25%;
            }

            .left2 {
                margin-top: 4vh;
                width: 25%;
            }
        
            .right {
                margin-bottom: 3vh;
                margin-left: 3vw;
                width: 50%;
            }
            .right2 {
                margin-bottom: 3vh;
                width: 50%;
            }

            h3 {
                margin-bottom: 5vh;
                margin-top: 3vh;
            }
        }
    }

    @media only screen and (max-width: 1100px) {
       #roadmap {
        .title {
            margin-bottom: 15vh;
         }
         .right {
            margin-bottom: 3vh;
            margin-left: 3vw;
            width: 55%;
        }
        .left {
            margin-top: 2vh;
            width: 22%;
        }

        img {
            width: 100px;
            height: 100px;
        }
        .right2 {
            margin-bottom: 3vh;
            margin-left: 3vw;
            width: 55%;

            h2 {
                font-size: 27px;
                line-height: 33px;
            }
        }
        .left2 {
            margin-top: 2vh;
            width: 22%;
        }
        
        h2 {
            font-size: 27px;
            line-height: 33px;
        }
        h3 {
            font-size: 21px;
            line-height: 20px;
            margin-bottom: 5vh;
            margin-top: 3vh;
        }
    }
}
      
      @media only screen and (max-width: 850px) {
        #roadmap {
            padding-top: 12vh;
            h1 {
                font-size: 40px;
                line-height: 40px;
              }
              
            .title {
                margin-bottom: 15vh;

                h3 {
                    font-size: 18px;
                    line-height: 22px;
                }
             }
             .right {
                margin-bottom: 3vh;
                margin-left: 3vw;
                width: 60%;
            }
            .left {
                margin-top: 2vh;
                width: 22%;
            }
    
            img {
                width: 80px;
                height: 80px;
            }
            .right2 {
                margin-bottom: 3vh;
                margin-left: 3vw;
                width: 60%;
    
                h2 {
                    font-size: 25px;
                    line-height: 27px;
                }
            }
            .left2 {
                margin-top: 2vh;
                width: 22%;
            }

            h2 {
                font-size: 25px;
                line-height: 29px;
            }
            h3 {
                font-size: 18px;
                margin-bottom: 4vh;
                margin-top: 2vh;
            }
        }
    }
      
      @media only screen and (max-width: 600px) {
        #roadmap {
            hr {display: none;}
            padding-bottom: 15vh;

            h1 {
                font-size: 30px;
                line-height: 30px;
            }

            h1::before {
                top: -6px;
                left: -12px;
                width: 9px;
                height: 9px;
            }
        
            h1::after {
                top: -6px;
                right: -10px;
                width: 9px;
                height: 9px;
            }
              
            .title {
                margin-bottom: 12vh;
                
                h3 {
                    margin-left: 20px;
                    text-align: center;
                    font-size: 16px;
                    line-height: 16px;
                }
             }
             .right {
                margin-bottom: 3vh;
                margin-left: 5vw;
                width: 90%;
            }
            .left {
                margin-top: 2vh;
                width: 25%;
            }
    
            img {
                width: 55px;
                height: 55px;
            }
            .right2 {
                margin-bottom: 3vh;
                margin-left: 5vw;
                width: 90%;
    
                h2 {
                    width: 98%;
                    font-size: 16px;
                    line-height: 16px;
                }
            }
            .left2 {
                margin-top: 2vh;
                width: 25%;
            }

            h2 {
                width: 95%;
                font-size: 17px;
                line-height: 17px;
            }
            h3 {
                font-size: 16px;
                line-height: 14px;
                margin-bottom: 4vh;
                margin-top: 2vh;
                margin-right: 50px;
                width: 90%;
                word-spacing: normal;
            }
        }
      }
      
