.outer-cursor {
    z-index: 100;
    position: fixed;
    width: 40px;
    height: 40px;
    left: 10px;
    background: transparent;
    border-radius: 25% 10%;
    pointer-events: none; 
    mix-blend-mode: difference;
    transform: translate(-50%, -50%) rotate(45deg);
    -webkit-box-shadow: 0px 20px 0px -10px #FFFFFF, 0px -0px 0px -10px #FFFFFF, 20px 0px 0px -10px #FFFFFF, -20px 0px 0px -10px #FFFFFF, 0px 0px 0px 10px transparent, 5px 5px 15px 5px rgba(0,0,0,0); 
    box-shadow: 0px 14px 0px -10px #FFFFFF, 0px -14px 0px -10px #FFFFFF, 14px 0px 0px -10px #FFFFFF, -14px 0px 0px -10px #FFFFFF, 0px 0px 0px 10px transparent, 0px 0px 0px 0px rgba(0,0,0,0);
  }

  .outer-cursor1 {
    width: 40px;
    height: 40px;
    z-index: 0;
    position: fixed;
    border: 8px solid white;
    border-radius: 50%;
    text-shadow: 0 0 20px white;
    opacity: .5;
    mix-blend-mode: difference;
    transform: translate(-10%, -10%);
    animation: cursorAnim2 .5s infinite alternate;
}

.inner-cursor {
    z-index: 1002;
    position: fixed;
    left: 10px;
    width: 10px;
    height: 10px;
    transform: translate(-50%, -50%);
    background-color: #FFFFFF;
    mix-blend-mode: difference;
    border-radius: 50%;
    pointer-events: none;
    transition: width 0.5s, height 0.5s;
}

.inner-cursor.grow {
    width: 25px;
    height: 25px;
    transition: width 0.5s, height 0.5s;
}

@keyframes cursorAnim {
    from {
        transform: scale(1);
    }
    to {
        transform: scale(3);
    }
}

@keyframes cursorAnim2 {
    from {
        transform: scale(0.6);
    }
    to {
        transform: scale(.4);
    }
}

@keyframes cursorAnim3 {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(3);
    }
    100% {
        transform: scale(1);
        opacity: 0;
    }
}

.expand {
    animation: cursorAnim3 .5s forwards;
    border: 1px solid green;
}

@media only screen and (max-width: 600px) {
    body {
        cursor: unset;
    }

    .outer-cursor {
       display: none;
    }
    .outer-cursor1 {
        display: none;
    }
    .inner-cursor {
        display: none;
    }


}