@import url("https://use.typekit.net/jmk3xov.css");
@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');

@font-face {
  font-family: "Söhne Breit Test";
  src: url("./../fonts/BreitTest.ttf") format("truetype"),
 }

 @font-face {
  font-family: "Founders Grotesk Condensed Test Regular";
  src: url("./../fonts/test-founders-grotesk-condensed-regular.ttf") format("truetype"),
 }

 @font-face {
  font-family: "Founders Grotesk Condensed Test Medium";
  src: url("./../fonts/test-founders-grotesk-condensed-medium.ttf") format("truetype"),
 }

 @font-face {
  font-family: "Founders Grotesk Condensed Test Semibold";
  src: url("./../fonts/test-founders-grotesk-condensed-semibold.ttf") format("truetype"),
 }
 
canvas {
  position: fixed;
  top: 0;
  left: 0;
}

body {
  margin: 0;
  width: 100%;
  background-color: black;
  cursor: none;
}

:root {
  --dark-bg: rgba(15, 15, 15, 0.95);
}

main {
  color: white;
  background-color: transparent;
  z-index: 99;
  position: absolute;
  width: 100%;
}

a {
  text-decoration: inherit;
  text-align: center;
}


  #meta_manager {
    position: fixed;
    z-index: 99;
    top: 0;
    left: 2%;
    font-family: "Söhne Breit Test";
    font-weight: 600;
    font-size: 25px;
    line-height: 29px;
    color: #FFFFFF;
    animation-delay: 100s;
  }

  @keyframes opac {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes animate {
  0% {
      filter: blur(0);
  }

  40% {
      filter: blur(20px);
  }

  80% {
      filter: blur(0);
  }

  100% {
      filter: blur(0);
  }
}

  #cta {
    position: fixed;
    width: 140px;
    white-space: nowrap;
    z-index: 99;
    top: 20px;
    right: 50px;
    font-family: "Söhne Breit Test";
    font-size: 16px;
    line-height: 19px;
    padding: 18px;
    color: #ffffff;
    font-weight: bold;
    background-image: url('./../img/button.png');
    background-size: cover;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.94) inset;
    transition: 1s all ease;
  }

  #cta::before {
    color: black;
    content: '';
    position: absolute;
    top: 50%;
    z-index: -1;
    left: 50%;
    transform: translate(-50%, -50%);
    transition:  all 0.3s ease;
    width: 0%;
    height: 100%;

  }

  #cta:hover::before {
    width: 100%;
    color: black;
    background-color: white;
    z-index: -1;
  }

  #cta:hover {
    color:#001AFF;
  }

  #twitter {
    font-family: "Söhne Breit Test";
    font-size: 16px;
    line-height: 19px;
    color: #FFFFFF;
    white-space: nowrap;
    position: fixed;
    width: 140px;
    padding: 15px 0;
    z-index: 99;
    bottom: 30px;
    right: 45px;
    border: 2px solid #FFFFFF;
    box-sizing: border-box;
    border-radius: 5px;
    transition: 1s all ease;
    overflow: hidden;

    img {
      text-align: right;
      margin: -2% 7%;
    }

  }

  #twitter::before {
    color: black;
    content: '';
    background-color: white;
    position: absolute;
    top: 50%;
    z-index: -1;
    left: 50%;
    transform: translate(-50%,-50%);
    transition:  all 0.4s ease;
    width: 120%;
    height: 0%;
    transform: translate(-50%,-50%)rotate(25deg);
  }
  #twitter:hover::before {
    height: 215%;
    color: black;
    background-color: white;
    z-index: -1;
  }
  
  #twitter:hover {
    color: black;

    img {
      filter: brightness(50%) sepia(1) hue-rotate(179.4deg  ) saturate(101.4%) brightness(0%);
    }
  }
  
  #discord {
      font-family: "Söhne Breit Test";
      font-size: 16px;
      line-height: 19px;
      color: #FFFFFF;
      position: fixed;
      white-space: nowrap;
      width: 140px;
      padding: 15px 0;
      z-index: 99;
      bottom: 94px;
      right: 45px;
      border: 2px solid #FFFFFF;
      box-sizing: border-box;
      border-radius: 5px;
      overflow: hidden;
  
      img {
        margin: -2% 6%;
        text-align: right;
      }      
    }
    
    #discord::before {
    color: black;
    content: '';
    background-color: white;
    position: absolute;
    top: 50%;
    z-index: -1;
    left: 50%;
    transform: translate(-50%,-50%);
    transition:  all 0.4s ease;
    width: 120%;
    height: 0%;
    transform: translate(-50%,-50%)rotate(25deg);
  }

  #discord:hover::before {
    height: 215%;
    color: black;
    background-color: white;
    z-index: -1;
  }

  #discord:hover {
    color: black;
    
    img {
      filter: brightness(50%) sepia(1) hue-rotate(179.4deg  ) saturate(101.4%) brightness(0%);
    }
  }

  .fixed_left {
    display: block;
  }

  #drop_soon::before {
    position: absolute;
    display: inline-block;
    content: "";
    top: -15px;
    left: 60px;
    width: 200px;
    height:3.5px;
    background: #fff;
  }
  
  #drop_soon {
    position: fixed;
    font-family: "Söhne Breit Test";
    font-size: 20px;
    line-height: 23px;
    color: #FFFFFF;
    z-index: 99;
    bottom: 3%;
    left: 2%;
  }

  #drop_soon::after {
    z-index: -1;
    position: absolute;
    display: inline-block;
    content: "";
    top: -40px;
    left: -10px;
    // background-image: url('./../img/data_carre.png');
    background: linear-gradient(to top, rgb(39, 39, 250), rgb(93, 93, 223));
    // background-size: contain;
    width: 75px;
    height: 75px;
    // width: 130px;
    // height: 130px;
    transform: rotate(45deg);
    }

    #drop_soon:hover::after {
      border: 2px solid #FFFFFF;
      background: transparent;
      animation: animate 1s linear forwards;
      -webkit-box-shadow: 0px 0px 0px 5px rgb(39, 39, 250); 
      box-shadow: 0px 0px 0px 5px rgb(39, 39, 250);
    }

    @keyframes animate {
      0% {
          filter: blur(0);
      }

      40% {
          filter: blur(20px);
      }

      80% {
          filter: blur(0);
      }

      100% {
          filter: blur(0);
      }
  }

    @media only screen and (max-width: 1000px) {

    #drop_soon::before {
      // top: -15px;
      left: 58px;
      width: 177px;
      // height:3.5px;

      // top: -15px;
      // left: 60px;
      // width: 200px;
      // height:3.5px;
    }
    
    #drop_soon {
      font-size: 18px;
      line-height: 20px;
      bottom: 3%;
      left: 4%;
    }
  
    #drop_soon::after {
      // top: -65px;
      // left: -30px;
      width: 67px;
      height: 67px;
  }

    #cta {
      width: 130px;
      font-size: 15px;
      line-height: 19px;
      padding: 15px;
    }

    #meta_manager {
      top: 0;
      left: 4%;
      font-size: 20px;
      line-height: 29px;
    }

    #discord {
      width: 130px;
      padding: 15px 0;
  }
  #twitter {
    width: 130px;
    padding: 15px 0;
  }

  }
  

  @media only screen and (max-width: 700px) {

    .fixed_left {
      display: none;
    }

    header {
      font-size: 10px;
      line-height: 2px;
    }

    #meta_manager {
      display: none;
    }
    #cta {
      width: 110px;
      font-size: 15px;
      right: 30px;
      line-height: 16px;
      padding: 15px;
    }

    #discord {
      font-size: 0px;
      line-height: 0px;
      width: 50px;
      padding: 15px 0;
      bottom: 30px;
      right: 90px;
  }
  #twitter {
    font-size: 0px;
    line-height: 0px;
    width: 50px;
    padding: 15px 0;
    right: 30px;
  }
  }
  
  @media only screen and (max-width: 550px) {
    #cta {
      width: 100px;
      font-size: 14px;
      right: 30px;
      line-height: 16px;
      padding: 15px;
    }

  }
  